
<template>
  <div id="app">
    <div class="topbar-main">
      <el-row>
        <el-col :span="4" align="left">
          <i class="el-icon-info"></i><span style="font-size: 18px;color: #fff;">内容管理后台管理系统</span>
        </el-col>
        <el-col :span="15">
          &nbsp;
        </el-col>
        <el-col :span="2" style="font-size: 16px;color: #fff;align: right;">
          <span >登录用户：{{username}}</span>
        </el-col>
        <el-col :span="3">
          <el-button :plain="true" size="mini" @click="logout">注销</el-button>
        </el-col>
      </el-row>
    </div>
    <el-row class="container">
    <!--头部-->
      <el-col :span="2" class="topbar">
        
        <!--左侧导航-->
      
        <el-menu default-active="1" class="el-menu-vertical-demo">
          <el-menu-item index="1">
            <i class="el-icon-setting"></i>
            <span slot="title"><router-link to="/ParameterManager">参数设置</router-link></span>
          </el-menu-item>
          <el-menu-item index="2">
            <i class="el-icon-location"></i>
            <span slot="title"><router-link to="/ComponentManager">组件设置</router-link></span>
          </el-menu-item>
          <el-menu-item index="3">
            <i class="el-icon-share"></i>
            <span slot="title"><router-link to="/ActionManager">事件设置</router-link></span>
          </el-menu-item>
          <el-menu-item index="4">
            <i class="el-icon-menu"></i>
            <span slot="title"><router-link to="/MenuManager">菜单设置</router-link></span>
          </el-menu-item>
          <el-menu-item index="5">
            <i class="el-icon-tickets"></i>
            <span slot="title"><router-link to="/FormManager">表单设置</router-link></span>
          </el-menu-item>
          <el-menu-item index="6">
            <i class="el-icon-tickets"></i>
            <span slot="title"><router-link to="/GridViewManager">列表设置</router-link></span>
          </el-menu-item>
          <el-menu-item index="7">
            <i class="el-icon-search"></i>
            <span slot="title"><router-link to="/QueryManager">查询设置</router-link></span>
          </el-menu-item>
          <el-menu-item index="8">
            <i class="el-icon-d-caret"></i>
            <span slot="title"><router-link to="/SelectValueManager">选项设置</router-link></span>
          </el-menu-item>
          <el-menu-item index="9">
            <i class="el-icon-search"></i>
            <span slot="title"><router-link to="/CategoryManager">系统元数据</router-link></span>
          </el-menu-item>
        </el-menu>
      </el-col>
      <el-col :span="22">
        <transition name="fade" mode="out-in">
          <router-view></router-view>
        </transition>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  name: 'App',
  data() {
    return {
       username:''
    };
  },
  beforeCreate()
  {
    var user = sessionStorage.getItem('access-user');
    if(!user)
    {
	    this.$router.push({path: '/Login'});
    }
  },
  mounted(){ 
    this.$nextTick(function () {
      setInterval(this.checklogin, 1000);
    })
  },
  methods: {
    logout() {
      sessionStorage.removeItem('access-user');
      this.$router.push({path: '/Login'});
    },
    checklogin()
    {
      var user = sessionStorage.getItem('access-user');
      if(user)
      {
        this.username = JSON.parse(sessionStorage.getItem('access-user')).username;
      }
      else
      {
        this.username = '';
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
  margin-left: 0px;
}
</style>



