import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
// import Home from '@/components/Home'
import ParameterManager from '@/components/ParameterManager'
import ComponentManager from '@/components/ComponentManager'
import ActionManager from '@/components/ActionManager'
import MenuManager from '@/components/MenuManager'
import FormManager from '@/components/FormManager'
import FormItemManager from '@/components/FormItemManager'
import GridViewManager from '@/components/GridViewManager'
import GridViewItemManager from '@/components/GridViewItemManager'
import CategoryManager from '@/components/CategoryManager'
import QueryManager from '@/components/QueryManager'
import SelectValueManager from '@/components/SelectValueManager'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: '登录',
      component: Login
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/',
      name: '首页',
      component: ParameterManager
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/ParameterManager',
      component: ParameterManager,
      name: '参数管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/ComponentManager',
      component: ComponentManager,
      name: '组件管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/ActionManager',
      component: ActionManager,
      name: '事件管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/MenuManager',
      component: MenuManager,
      name: '菜单管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/FormManager',
      component: FormManager,
      name: '表单管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/FormItemManager',
      component: FormItemManager,
      name: '表单项管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/GridViewManager',
      component: GridViewManager,
      name: '列表管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/GridViewItemManager',
      component: GridViewItemManager,
      name: '列表项管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/CategoryManager',
      component: CategoryManager,
      name: '元数据查询'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/QueryManager',
      component: QueryManager,
      name: '查询管理'
    },
    {
      meta: {
        requireAuth: true
      },
      path: '/SelectValueManager',
      component: SelectValueManager,
      name: '选项管理'
    }
  ]
})



