<template>
  <div>
    <el-table
                :data="dataList"
                border
                height="400"
                v-loading="loading"
                style="width: 100%">
      <el-table-column
      type="selection"
      width="40">
    </el-table-column>
      <el-table-column label="" type="index" width="60">
      </el-table-column>
      <div v-for="(citem,index) in gridList">
        <div v-if="citem.visibleType==1">
          <el-table-column :prop="citem.attrName" :label="citem.label" :width="citem.width">
          </el-table-column>
        </div>
      </div>
    </el-table>
  </div>
</template>

<script type="text/javascript">

export default {
  name: "GridViewItemCheck",
  data() {
    return {
      dataList: [],
      gridList: [],
      loading: false,
      form: {
        name: "",
        description: "",
        value: "",
        visibleType: "1",
        width: "120",
        orderIndex: 1
      },
    };
  },
  props: {
    parentgridid:""
  },
  beforeCreate()
  {
    var user = sessionStorage.getItem('access-user');
    if(!user)
    {
	    this.$router.push({path: '/Login'});
    }
  },
   created(){ 
     if(this.$route.query.parentid)
     {
       this.parentgridid = this.$route.query.parentid;
     }
     this.loaddata();
    },
  methods: {
    loaddata()
    {
      let _self = this;
      _self.loading = true;
      
      //alert(_self.parentid);
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        method: 'post',
        data: _self.parentgridid,
        url: '/api/admin/getGridViewItem'
      })
        .then(function(response) {
          _self.gridList = response.data.list;
          _self.loading = false;
          console.log(JSON.stringify(_self.gridList));
          _self.refreshData();
        })
        .catch(function(error) {
          console.log(error);
          _self.loading = false;
        });
    },
    refreshData() {
      var i;
      let tab = _self.gridList;
      for(i=0;i<10;i++)
      {
         var j;
         var rowdata=[];
         for (j in tab) {
           //rowdata.push({(j.attrName,j.attrName)});
         }
      }
    },
    search() {
      let _self = this;
      let tab = _self.dataListFull;
      _self.dataList = [];
      var i;
      if (_self.inputkey != "" || _self.parentid != "") {
        for (i in tab) {
          if (_self.inputkey != "") {
            if (
              tab[i].attrName.indexOf(_self.inputkey) >= 0 ||
              tab[i].label.indexOf(_self.inputkey) >= 0
            ) {
              if (_self.parentid != "") {
                if (tab[i].parentId == _self.parentid) {
                  _self.dataList.push(tab[i]);
                }
              } else {
                _self.dataList.push(tab[i]);
              }
            }
          } else if (_self.parentid != "") {
            //_self.$message(tab[i].parentId+","+_self.parentid);
            if (tab[i].parentId == _self.parentid) {
              _self.dataList.push(tab[i]);
            }
          }
        }
      } else {
        _self.dataList = _self.dataListFull;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

</style>



