<template>
  <div>
    <el-row  class="topbar">
      <el-col :span="4">
        <div class="topbar">
          <el-input  v-model="inputkey" placeholder="请输入关键字" @change="search" prefix-icon="el-icon-search"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-table
                :data="dataList"
                border
                height="640"
                v-loading="loading"
                style="width: 100%">
        <el-table-column prop="name" label="名称" width="160" sortable>
        </el-table-column>
        <el-table-column prop="description" label="说明" width="160" sortable>
        </el-table-column>
        <el-table-column prop="labelColumn" label="标签列" width="80" sortable>
        </el-table-column>
        <el-table-column prop="valueColumn" label="值列" width="80" sortable>
        </el-table-column>
        <el-table-column label="操作"  width="80">
          <template slot-scope="scope">
            <el-button :plain="true" type="primary" size="small" icon="edit" @click="selectitem(scope.row)">选择</el-button>
          </template>
        </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/javascript">
// $.ajaxSetup({
//   contentType: "application/json"
// });

export default {
  name: "SelectQuery",
  data() {
    return {
      dataList: [],
      dataListFull: [],
      inputkey: "",
      loading: false
    };
  },
  beforeCreate()
  {
    var user = sessionStorage.getItem('access-user');
    if(!user)
    {
	    this.$router.push({path: '/Login'});
    }
  },
   created(){ 
     
    let _self = this;
    _self.loading = true;
    _self.axios({
      headers: {
        "Content-Type": "application/json;charset=UTF-8"
      },
      method: 'get',
      url: '/api/admin/getQuery'
    })
      .then(function(response) {
        _self.dataListFull = response.data.list;
        _self.dataList = response.data.list;
        _self.loading = false;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading = false;
      });

    },
  methods: {
    refreshData() {
      let _self = this;
      _self.loading = true;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        method: 'get',
        url: '/api/admin/getQuery'
      })
      .then(function(response) {
        _self.dataListFull = response.data.list;
        _self.dataList = response.data.list;
        _self.loading = false;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading = false;
      });
    },
    selectitem(indata) {
      let _self = this;
      _self.$emit('onqueryselected',indata);
    },
    search() {
      let _self = this;
      let tab = _self.dataListFull;
      if (_self.inputkey != "") {
        _self.dataList = [];
        var i;
        for (i in tab) {
          if (tab[i].name.indexOf(_self.inputkey) >= 0||tab[i].description.indexOf(_self.inputkey) >= 0) {
            _self.dataList.push(tab[i]);
          }
        }
      } else {
        _self.dataList = _self.dataListFull;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



