<template>
  <div >
    <el-row class="topbar">
      <el-col :span="4">
        <div class="topbar">
          系统元数据
        </div>
      </el-col>
      <el-col :span="4">
        <div class="topbar">
          <el-input  v-model="inputkey" placeholder="请输入关键字" @change="search" prefix-icon="el-icon-search"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4">
        <el-table
                  :data="catList"
                  border
                  height="520"
                  v-loading="loading"
                  style="width: 100%">
          <el-table-column label="类别名称" sortable align="left">
            <template slot-scope="scope">
              <el-button type="primary"  @click="showitem(scope.row)">{{scope.row.name}}</el-button>
            </template>
          </el-table-column>
        </el-table>  
        </el-col>
        <el-col :span="16">
          <el-table
                    :data="dataList"
                    border
                    height="520"
                    v-loading="loading2"
                    style="width: 100%">
            <el-table-column
              label="行号"
              type="index"
              width="60">
            </el-table-column>
            <el-table-column prop="name" label="名称" width="120" sortable>
            </el-table-column>
            <el-table-column label="完整名称" width="140">
              <template slot-scope="scope">
                <el-input  v-model="scope.row.fullName"></el-input>
              </template>
            </el-table-column> 
            <el-table-column label="操作" width="80">
              <template slot-scope="scope">
                <el-button :plain="true" type="primary" size="small" icon="edit" @click="selectitem(scope.row)">选择</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
    </el-row>
  </div>
</template>

<script type="text/javascript">
// $.ajaxSetup({
//   contentType: "application/json"
// });

export default {
  name: "CategoryManager",
  data() {
    return {
      catList:[],
      dataList:[],
      dataListFull:[],
      inputkey:"",
      loading:false,
      loading2:false,
      currrentType:"",
      formLabelWidth: "120px"
    };
  },
  beforeCreate()
  {
    var user = sessionStorage.getItem('access-user');
    if(!user)
    {
	    this.$router.push({path: '/Login'});
    }
  },
   created(){ 
     
    let _self = this;
    _self.loading = true;
    _self.axios({
      headers: {
        "Content-Type": "application/json;charset=UTF-8"
      },
      method: 'get',
      url: '/api/admin/getCategories'
    })
      .then(function(response) {
        _self.catList = response.data.list;
        _self.loading = false;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading = false;
      });

    },
  methods: {
    refreshData() {
      let _self = this;
      _self.loading = true;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        method: 'get',
        url: '/api/admin/getCategories'
      })
      .then(function(response) {
        _self.catList = response.data.list;
        _self.loading = false;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading = false;
      });
    },
    showitem(indata) {
      let _self = this;
      _self.loading2 = true;
      _self.currentType = indata.name;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        datatype: 'json',
        method: 'post',
        data: JSON.stringify(indata),
        url: '/api/admin/getCategoryAttrs'
      })
      .then(function(response) {
        _self.dataList = response.data.list;
        _self.dataListFull = response.data.list;
        _self.loading2 = false;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading2 = false;
      });
    },
    selectitem(indata) {
      let _self = this;
      indata.typeName=_self.currentType;
      _self.$emit('onselected',indata);
    },
    search() {
      let _self = this;
      let tab = _self.dataListFull;
      if (_self.inputkey != "") {
        _self.dataList = [];
        var i;
        for (i in tab) {
          if (tab[i].name.indexOf(_self.inputkey) >= 0) {
            _self.dataList.push(tab[i]);
          }
        }
      } else {
        _self.dataList = _self.dataListFull;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



