<template>
  <div>
    <div class="navbar">
      <span class="navbar">/列表项管理</span>
    </div>
    <el-dialog title="选择属性" :visible.sync="categoryVisible" >
      <CategoryManager ref="CategoryManager" @onselected="onselected" width="560" v-bind:categoryVisible="categoryVisible"></CategoryManager>
      <div slot="footer" class="dialog-footer">
        <el-button @click="categoryVisible = false">取 消</el-button>
      </div>
    </el-dialog>
    <el-dialog width="90%" title="列表校验" :visible.sync="checkVisible" >
      <GridViewItemCheck ref="GridViewItemCheck" width="560" :parentgridid="parentid"></GridViewItemCheck>
      <div slot="footer" class="dialog-footer">
        <el-button @click="checkVisible = false">取 消</el-button>
      </div>
    </el-dialog>
        <el-dialog title="添加" :visible.sync="dialogVisible">
          <el-form :model="form">
            <el-form-item label="父Id" :label-width="formLabelWidth">
              <el-input v-model="form.parentId" auto-complete="off"></el-input>
            </el-form-item>
             <el-row>
              <el-col span="10">
            <el-form-item label="属性名称" :label-width="formLabelWidth">
              <el-input v-model="form.attrName" auto-complete="off"></el-input>
            </el-form-item>
            </el-col>
              <el-col span="10">
            <el-form-item label="标签" :label-width="formLabelWidth">
              <el-input v-model="form.label" auto-complete="off"></el-input>
            </el-form-item>
            </el-col>
              <el-col span="4">
                <el-button @click="showcategory('')">选择属性</el-button>
              </el-col>
            </el-row>
            <el-form-item label="宽度" :label-width="formLabelWidth">
              <el-input v-model="form.width" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="显示类型" :label-width="formLabelWidth">
            <el-select v-model="form.visibleType">
                <el-option label="1" value="1"></el-option>
                <el-option label="2" value="2"></el-option>
                <el-option label="3" value="3"></el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="序号" :label-width="formLabelWidth">
              <el-input v-model="form.orderIndex" auto-complete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="additem(form)">确 定</el-button>
          </div>
        </el-dialog>
    
          <el-row class="topbar">
            <el-col :span="4">
                列表：{{typename}}
              </el-col>
            <el-col :span="2">
            <div class="topbar">
                <el-input  v-model="parentid" placeholder="请输入父Id" @change="search" prefix-icon="el-icon-search"></el-input>
            </div>
            </el-col>
            <el-col :span="4">
            <div class="topbar">
                <el-input  v-model="inputkey" placeholder="请输入属性名关键字" @change="search" prefix-icon="el-icon-search"></el-input>
            </div>
            </el-col>
           <el-col :span="1">
              <el-tooltip  class="item" effect="dark" content="新增" placement="top">
                <el-button type="primary" icon="el-icon-edit" circle @click="dialogVisible =true"></el-button>
              </el-tooltip>  
            </el-col>
            <el-col :span="1">
              <el-tooltip  class="item" effect="dark" content="列表验证" placement="top">
                <el-button type="primary" icon="el-icon-check" circle @click="startcheck()"></el-button>
              </el-tooltip>
            </el-col>  
          </el-row>
    <el-table
                :data="dataList"
                border
                height="640"
                v-loading="loading"
                style="width: 100%">
        <el-table-column prop="id" label="ID" width="80" sortable>
        </el-table-column>
        <el-table-column label="父ID" width="80">
          <template slot-scope="scope">
            <el-input  v-model="scope.row.parentId"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="属性名" width="180">
          <template slot-scope="scope">
            <el-input  v-model="scope.row.attrName"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="标签" width="180">
          <template slot-scope="scope">
            <el-input  v-model="scope.row.label"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="宽度" width="180">
          <template slot-scope="scope">
            <el-input  v-model="scope.row.width"></el-input>
          </template>
        </el-table-column> 
        <el-table-column label="显示类型" width="120">
          <template slot-scope="scope">
            <el-select v-model="scope.row.visibleType">
              <el-option label="1" value="1"></el-option>
              <el-option label="2" value="2"></el-option>
              <el-option label="3" value="3"></el-option>
            </el-select>
          </template>
        </el-table-column> 
        <el-table-column label="序号" width="120">
          <template slot-scope="scope">
            <el-input  v-model="scope.row.orderIndex"></el-input>
          </template>
        </el-table-column> 
        <el-table-column label="操作" width="240">
          <template slot-scope="scope">
            <el-button :plain="true" type="primary" size="small" icon="edit" @click="showcategory(scope.row)">选择</el-button>
            <el-button :plain="true" type="primary" size="small" icon="edit" @click="saveitem(scope.row)">保存</el-button>
            <el-button :plain="true" type="danger" size="small" icon="delete" @click="delitem(scope.row)">删除</el-button>
          </template>
        </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/javascript">
// $.ajaxSetup({
//   contentType: "application/json"
// });
// });
import CategoryManager from '@/components/CategoryManager'
import GridViewItemCheck from '@/components/GridViewItemCheck'

export default {
  name: "GridViewItemManager",
  data() {
    return {
      dataList: [],
      dataListFull: [],
      inputkey: "",
      loading: false,
      dialogVisible: false,
      categoryVisible: false,
      checkVisible:false,
      typename:"",
      parentid:"",
      currentItem:"",
      form: {
        name: "",
        description: "",
        value: "",
        visibleType: "1",
        width: "120",
        orderIndex: 1
      },
      formLabelWidth: "120px",
      formLabelWidth2: "60px"
    };
  },
   components: {
     CategoryManager: CategoryManager,
     GridViewItemCheck: GridViewItemCheck
  },
  beforeCreate()
  {
    var user = sessionStorage.getItem('access-user');
    if(!user)
    {
	    this.$router.push({path: '/Login'});
    }
  },
   created(){ 
     
    let _self = this;
    _self.loading = true;
    var pid = _self.$route.query.parentid;
    _self.typename = _self.$route.query.name;
    if(pid)
    {
      _self.parentid = pid;
      _self.form.parentId = pid;
    }
    else
    {
      pid="0";
    }
    //alert(_self.parentid);
    _self.axios({
      headers: {
        "Content-Type": "application/json;charset=UTF-8"
      },
      method: 'post',
      data: pid,
      url: '/api/admin/getGridViewItem'
    })
      .then(function(response) {
        _self.dataListFull = response.data.list;
        _self.dataList = response.data.list;
        _self.loading = false;
        _self.form.orderIndex = _self.datalistFull.length;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading = false;
      });

    },
  methods: {
    startcheck()
    {
      let _self = this;
      _self.checkVisible = true;
      _self.$refs.GridViewItemCheck.loaddata();
    },
    refreshData() {
      let _self = this;
      _self.loading = true;
      var pid = _self.parentid;
      if(pid=="")
      {
        pid ="0";
      }
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        method: 'post',
        data: pid,
        url: '/api/admin/getGridViewItem'
      })
      .then(function(response) {
        _self.dataListFull = response.data.list;
        _self.dataList = response.data.list;
        _self.loading = false;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading = false;
      });
    },
    saveitem(indata) {
      let _self = this;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        datatype: 'json',
        method: 'post',
        data: JSON.stringify(indata),
        url: '/api/admin/updateGridViewItem'
      })
      .then(function(response) {
        _self.$message("保存成功!");
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    delitem(indata) {
      let _self = this;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        datatype: 'json',
        method: 'post',
        data: JSON.stringify(indata),
        url: '/api/admin/deleteGridViewItem'
      })
      .then(function(response) {
        _self.$message("删除成功!");
        _self.refreshData();
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    additem(indata) {
      let _self = this;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        datatype: 'json',
        method: 'post',
        data: JSON.stringify(indata),
        url: '/api/admin/newGridViewItem'
      })
      .then(function(response) {
          _self.dialogVisible = false;
          _self.refreshData();
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    showcategory(indata)
    {
      this.categoryVisible = true;
      this.currentItem = indata;
    },
    onselected(indata) {
     // alert(indata.fullName);
      let _self = this;
      _self.categoryVisible = false;
      if(indata)
      {
        if(_self.currentItem=='')
        {
          _self.form.attrName = indata.fullName;
          _self.form.label = indata.name;
        }
        else
        {
          _self.currentItem.attrName = indata.fullName;
          _self.currentItem.label = indata.name;
        }
      }
    },
    search() {
      let _self = this;
      let tab = _self.dataListFull;
      _self.dataList = [];
      var i;
      if (_self.inputkey != "" || _self.parentid != "") {
        for (i in tab) {
          if (_self.inputkey != "") {
            if (
              tab[i].attrName.indexOf(_self.inputkey) >= 0 ||
              tab[i].label.indexOf(_self.inputkey) >= 0
            ) {
              if (_self.parentid != "") {
                if (tab[i].parentId == _self.parentid) {
                  _self.dataList.push(tab[i]);
                }
              } else {
                _self.dataList.push(tab[i]);
              }
            }
          } else if (_self.parentid != "") {
            //_self.$message(tab[i].parentId+","+_self.parentid);
            if (tab[i].parentId == _self.parentid) {
              _self.dataList.push(tab[i]);
            }
          }
        }
      } else {
        _self.dataList = _self.dataListFull;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

</style>



