<template>
  <div>
    <div class="navbar">
      <span class="navbar">/参数管理</span>
    </div>
    <el-dialog title="选择属性" :visible.sync="categoryVisible" >
      <CategoryManager ref="CategoryManager" @onselected="onselected" width="560" v-bind:categoryVisible="categoryVisible"></CategoryManager>
      <div slot="footer" class="dialog-footer">
        <el-button @click="categoryVisible = false">取 消</el-button>
      </div>
    </el-dialog>
    <el-row  class="topbar">
      <el-col :span="4">
        <el-dialog title="添加" :visible.sync="dialogVisible">
          <el-form :model="form">
            <el-form-item label="名称" :label-width="formLabelWidth">
              <el-input v-model="form.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="说明" :label-width="formLabelWidth">
              <el-input v-model="form.description" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="值" :label-width="formLabelWidth">
              <el-input v-model="form.value" auto-complete="off"></el-input>
            </el-form-item>
            &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; <el-button @click="showcategory('')">选择属性</el-button>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="additem(form)">确 定</el-button>
          </div>
        </el-dialog>
        <div>
          <el-input  v-model="inputkey" placeholder="请输入关键字" @change="search" prefix-icon="el-icon-search"></el-input>
        </div>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" icon="el-icon-edit" circle @click="dialogVisible = true"></el-button>
      </el-col>
    </el-row>
    <el-table
                :data="dataList"
                border
                height="640"
                v-loading="loading"
                style="width: 100%">
      <el-table-column
        label="行号"
        type="index"
        width="60">
      </el-table-column>
        <el-table-column prop="name" label="名称" width="240" sortable>
        </el-table-column>
        <el-table-column label="说明" width="240">
          <template slot-scope="scope">
            <el-input  v-model="scope.row.description"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="值" width="320">
          <template slot-scope="scope">
            <el-input  v-model="scope.row.value"></el-input>
          </template>
        </el-table-column> 
        <el-table-column label="操作" width="240">
          <template slot-scope="scope">
            <el-button :plain="true" type="primary" size="small" icon="edit" @click="showcategory(scope.row)">选择</el-button>
            <el-button :plain="true" type="primary" size="small" icon="edit" @click="save(scope.row)">保存</el-button>
            <el-button :plain="true" type="danger" size="small" icon="delete" @click="del(scope.row)">删除</el-button>
          </template>
        </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/javascript">
// $.ajaxSetup({
//   contentType: "application/json"
// });
import CategoryManager from '@/components/CategoryManager'

export default {
  name: "ParameterManager",
  data() {
    return {
      dataList: [],
      dataListFull: [],
      inputkey: "",
      loading: false,
      dialogVisible: false,
      categoryVisible: false,
      currentIndex: -1,
      currentItem: "",
      form: {
        name: "",
        description: "",
        value: ""
      },
      formLabelWidth: "120px"
    };
  },
  components: {
     CategoryManager: CategoryManager
  },
  beforeCreate()
  {
    var user = sessionStorage.getItem('access-user');
    if(!user)
    {
	    this.$router.push({path: '/Login'});
    }
  },
   created(){ 
     
    let _self = this;
    _self.loading = true;
    _self.axios({
      headers: {
        "Content-Type": "application/json;charset=UTF-8"
      },
      method: 'get',
      url: '/api/admin/getSysParameter'
    })
      .then(function(response) {
        _self.dataListFull = response.data.list;
        _self.dataList = response.data.list;
        _self.loading = false;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading = false;
      });

    },
  methods: {
    refreshData() {
      let _self = this;
      _self.loading = true;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        method: 'get',
        url: '/api/admin/getSysParameter'
      })
      .then(function(response) {
        _self.dataListFull = response.data.list;
        _self.dataList = response.data.list;
        _self.loading = false;
      })
      .catch(function(error) {
        console.log(error);
        _self.loading = false;
      });
    },
    openmsg(msg) {
      this.$message('提示: '+msg);
    },
    save(indata) {
      let _self = this;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        datatype: 'json',
        method: 'post',
        data: JSON.stringify(indata),
        url: '/api/admin/updateSysParameter'
      })
      .then(function(response) {
        _self.openmsg("保存成功!");
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    del(indata) {
      let _self = this;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        datatype: 'json',
        method: 'post',
        data: JSON.stringify(indata),
        url: '/api/admin/deleteSysParameter'
      })
      .then(function(response) {
        _self.openmsg("删除成功!");
        _self.refreshData();
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    additem(indata) {
      let _self = this;
      _self.axios({
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        datatype: 'json',
        method: 'post',
        data: JSON.stringify(indata),
        url: '/api/admin/newSysParameter'
      })
      .then(function(response) {
          _self.dialogVisible = false;
          _self.refreshData();
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    showcategory(indata)
    {
      this.categoryVisible = true;
      this.currentItem = indata;
    },
    onselected(indata) {

      let _self = this;
      _self.categoryVisible = false;
      if(indata)
      {
        if(_self.currentItem=='')
        {
          _self.form.value = indata.fullName;
          _self.form.description = indata.typeName+":"+indata.name;
        }
        else
        {
          _self.currentItem.value = indata.fullName;
          _self.currentItem.description = indata.typeName+":"+indata.name;
        }
      }
    },
    search() {
      let _self = this;
      if (_self.inputkey != "") {
        _self.dataList = [];
        _self.dataListFull.forEach((value,index)=>{
            if(value.name.indexOf(_self.inputkey)>=0||value.description.indexOf(_self.inputkey)>=0)
            {
              _self.dataList.push(value);
            }
          }
        );
      } else {
       _self.dataList = _self.dataListFull;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



